import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home_page.vue'
import Siemreap from '../views/Siem_Reap'
import Login from '../views/Auth/Login'
import Sign from '../views/Auth/Sign'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/siemreap',
    name: 'Siemreap',
    component: Siemreap
  },
  {
    path : '/login',
    name : 'Login',
    component : Login, 
  },

  {
    path : '/signup',
    name : 'Sign Up',
    component : Sign,
  },
  
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
